  position: relative;
  flex: 1 1 auto;
  text-align: center;
}
@media (min-width: 768px) {
  .announcement-bar__middle:not(:first-child):not(:last-child) {
    max-width: 50%;
  }
}

.announcement-bar__right {
  flex: 2 0 0;
  text-align: end;
}

