  transform: translateY(-5px);
  transition: 0.3s ease-out;
  transition-property: transform, opacity;
  opacity: 0;
}

.js .cc-accordion-item.is-open:not(.is-closing) .cc-accordion-item__content {
  transform: translateY(0px);
  opacity: 1;
