.product_nav {
  position: fixed;
  top: var(--top);
  left:0;
  width: 100%;
  z-index: 100;
  background-color: #fff;
  border-bottom: 1px solid #e2e2e2;
  font-size: 14px;
}

.product_nav_body {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 14px 0;
  overflow-x: auto;
  max-width: 1400px;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 0 30px;
}

.product_nav_body .product_menu {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex: none;
  overflow: hidden;
}

.product_menu_item {
  padding: 14px 10px;
  color: #323232;
  cursor: pointer;
  position: relative;
  line-height: 20px;
}

.product_menu_item.active {
  font-weight: bold;
}
.product_menu_item.active:after {
  transform: translateY(0px);
  opacity: 1;
}
.product_menu_item:after {
  content: '';
  width: 100%;
  height: 2px;
  border-radius: 66px;
  background-color: rgb(253, 216, 0);
  bottom: 0;
  left: 0;
  display: block;
  position: absolute;
  transform: translateY(5px);
  opacity: 0;
  transition: 0.3s;
}
@media (max-width: 768px) {
  .product_nav_body {
    justify-content: flex-start;
    padding: 0 10px;
  }
}